
import { useAppSelector } from "../../../utils/hooks/useAppStore";
import image from "../../../images/done.svg";
import style from "./order-details.module.css";

function OrderDetails() {
  const { orderNumber, orderRequest, orderSuccess, text } = useAppSelector((state) => state.makeOrder)
  return (
    <div className={`${style.mainCont}`}>
      {orderRequest ? (
        <>
          <p className="mb-2 text text_type_main-medium">
           ...
          </p>
        </>
      ) : orderSuccess && orderNumber ? (
        <>
          <h2 className="mb-6 text text_type_digits-large">{orderNumber}</h2>
          <p className="text text_type_main-medium"> </p>
          <img src={image} alt=" " className="mt-15 mb-15" />
          <p className="mb-2 text text_type_main-default">
               
          </p>
          <span className="text text_type_main-default text_color_inactive">
                
          </span>
        </>
      ) : (
        <>
          <p className="mb-2 text text_type_main-medium">
            ,        -. 
               .   .     .
          </p>
          {console.log(`   : ${text}`)}
        </>
      )}
    </div>
  );
}
export default OrderDetails;
